import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute } from '@angular/router';
import { BacklogDetailBean } from '../../../bean/backlog-detail.bean';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { ApprovedItemsBean, BackApproveDetailBean } from './bean/approvedItems.bean';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';

@Component({
	selector: 'app-one-approved-items',
	templateUrl: './one-approved-items.component.html',
	styleUrls: ['./one-approved-items.component.scss'],
	providers: [CusOperationHttpService, Commfunc, CustomeHttpService, ConfirmationService]
})

export class OneApprovedItemsComponent implements OnInit {
	//
	tabMesg: any = tableMessage;
	headerTitle: string;//
	showModel: string;
	scheduleTypeId3: any = [];
	ch: any = CALENDAR_CN;
	list: any = [];//
	items: any = [];
	temData: Date = new Date();
	mesg: any = {};
	//
	total: number;
	//
	pageSize: number;
	pageNum: number;
	taskBean: BacklogDetailBean = new BacklogDetailBean();
	userId: string;
	showFlag: boolean = true;
	itemsSmallType: any = [
	];
	//
	msgs: Message[] = [];
	estimatedStartDateBegin: Date = null;
	estimatedStartDateTo: Date = null;
	first: number = 0;//
	inValue: any;
	//
	code: any;
	constructor(
		public customeHttpService: CustomeHttpService,
		public httpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router,
		public activatedRouter: ActivatedRoute
	) {
		this.codeValues() //
		//,
		this.scheduleTypeId3 = this.code['ScheduleType'];
	}

	ngOnInit() {
		this.taskBean.pageNum = 1;
		this.taskBean.pageSize = 10;
		let session = window.sessionStorage;
		if (session.getItem('para')) {
			session.removeItem('para');
		}
		// if(this.activatedRouter.snapshot.params['flag']=='2'){
		// 	this.taskBean.statusId = '1';
		// }
		this.activatedRouter.queryParams.subscribe(params => {
			if(params.flag=='2'){
				this.taskBean.statusId = '1';
			};
	  
		  })
		this.taskBean.scheduleTypeId = this.activatedRouter.snapshot.params['scheduleTypeId'];
		this.itemsBigChange();
		this.taskBean.scheduleName = this.activatedRouter.snapshot.params['scheduleName'];
		this.doQuery();
	}

	//
	doQuery() {
		this.customeHttpService.queryApprList(this.taskBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.total = data.total;
				this.list = data.resultList;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}
	//
	toCustView(data) {
		if (data.relationKey == 'party') {
			this.commfunc.setSessionData('custNo', data.partyOrGroupNo);
			window.open('#/pages/custom-view/general', '_blank')
		} else if (data.relationKey == 'partyGroup') {
			window.open(`#/pages/tzb/custom/custom-group/custom-group-view?groupId=${data.partyOrGroupNo}`, '_blank')
		}
	}
	//
	reclean() {
		this.taskBean.partyOrGroupNo='';
		this.taskBean.partyOrGroupName='';
	}

	//
	itemsBigChange() {
		let params = {
			taskTypeParentId: '03',
			taskTypeKey: 'itemsType'
		}
		this.httpService.taskTypeQry(params).subscribe(data => {
			let arr = data.taskTypeList;
			this.itemsSmallType = [{ label: '', value: '' }];
			if (arr) {
				arr.forEach(item => {
					this.itemsSmallType.push({ label: item.taskTypeName, value: item.taskTypeId });

				});
			}
		})
	}
	// 
	paginate(event) {
			this.taskBean.pageSize = event.rows * 1;
			let currentPage = event.page + 1;
			this.taskBean.pageNum = currentPage;
			this.first = (currentPage - 1) * this.taskBean.pageSize;
			this.doQuery()
		
	}
	toDisposeSchedule(item) {
		this.commfunc.doDealHis(item);
	}
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}

}
