import { Component, OnInit, OnChanges, Input } from '@angular/core';
import { Output } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { CALENDAR_CN } from '../../../../../../../../@uisftech/common/constant/config';
import { ContactInformation, LinkageTheme, PriorityQuery, CustomerSource } from '../../../../../custom-contact-new/custom-constant/custom-code-value';
import { QueryUnprocessedCommunicationEventBean } from '../../../../../custom-contact-new/custom-contact-list/bean/custom-contact-list.bean';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'customer-contact-detail',
  templateUrl: './customer-contact-detail.component.html',
  styleUrls: ['./customer-contact-detail.component.scss'],
  providers: [Commfunc]
})
export class CustomerContactDetailComponent implements OnInit, OnChanges {

  @Input() inValue: any;
  @Output() outValue = new EventEmitter();

  ch: any = CALENDAR_CN;
  deployTimeStart: any;
  contactWay: any;
  msgs: any;
  deployTimeEnd: any;
  workEffortId: any; // ID
  communicationEventId: any; // ID
  contactInformation: any = ContactInformation // 
  linkageTheme: any = LinkageTheme; // 
  triority: any = PriorityQuery; // 
  customerSource: any = CustomerSource; // 

  communicationEventBean: QueryUnprocessedCommunicationEventBean = new QueryUnprocessedCommunicationEventBean();

  constructor(
    public commfunc: Commfunc,
    public httpService: CusOperationHttpService,
  ) { }

  ngOnInit() {

  }

  ngOnChanges() {
    console.log(this.inValue)
    if (this.inValue) {
      // this.communicationEventBean= this.inValue;
      this.communicationEventId = this.inValue.communicationEventId;
      this.queryConWorkEffortDetailInfo();
    }
  }
  reset() {
    this.outValue.emit({ dispaly: false })
  }

  // 
  queryConWorkEffortDetailInfo() {
    let param = {
      workEffortId: this.workEffortId, //	ID
      communicationEventId: this.communicationEventId, //	ID

    }
    this.httpService.queryConWorkEffortDetailInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.communicationEventBean = data
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }
  //
  tocust() {
    this.commfunc.setSessionData('custNo', this.communicationEventBean.custId); // by zj
    window.open('#/pages/custom-view/general', '_blank')
  }
}
