import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { taskpoolPageBean } from './bean/handling-matters.bean';
import * as $ from 'jquery';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { DomSanitizer } from '@angular/platform-browser';
/**
 * 
 */
@Component({
  selector: 'handling-matters',
  templateUrl: './handling-matters.component.html',
  styleUrls: ['./handling-matters.component.scss',
    './../menu-tree-ul.component.scss'
  ],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class HandlingMattersComponent implements OnInit, OnChanges {
  disinNav: boolean = false;
  inNav: any;
  Furl: string;
  listUrl: any;
  list: any = [];//
  startTime: Date = null;
  endTime: Date = null;
  ch: any = CALENDAR_CN;//
  first: number;
  userId: string = '';
  flag: string;
  //
  msgs: Message[] = [];
  menuList: any;
  taskpoolPageBean: taskpoolPageBean = new taskpoolPageBean();
  //
  code: any;
  ulData: any = [];
  constructor(
    public router: Router,
    public routeInfo: ActivatedRoute,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
    private sanitizer: DomSanitizer
  ) {
    this.codeValues() //
    this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
  }
  num: any;
  startDate: any;
  endDate: any;
  ngOnInit() {
    //
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let a = commonHeader.extParam.operatingLevel;

    this.activatedRouter.queryParams.subscribe(params => {
      this.flag = params.flag;
      this.num = params.totalNum;
      if (params.startDate) {
        this.startDate = params.startDate;
        this.endDate = params.endDate;
        console.log(this.startDate, this.endDate)
      }
    })
    this.queryNewList();
    if (this.commfunc.getSessionDataCH('markFlag')) {
      this.commfunc.clearSession('markFlag');
    }
    if (this.commfunc.getSessionDataCH('taskId')) {
      this.commfunc.clearSession('taskId');
    }
    if (this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')) {
      this.commfunc.clearSession('bpmQueryTaskOnlyList');
    }
  }
  ngOnChanges() {
    this.ngOnInit()
  }
  bussinessWhere: string;
  startUserId: any;
  nodata: boolean = false;
  maskLayer: boolean = true;
  // 
  queryNewList() {
    this.nodata = false;
    this.maskLayer = true;
    let nodeType;
    let bussinessWhere;
    let startUserId;
    let session = window.sessionStorage;
    if (session.getItem('bussinessWhere')) {
      let bussinessWhere = session.getItem('bussinessWhere');
      if (bussinessWhere.indexOf('startUserId') != -1) {
        let t = bussinessWhere.split('=');
        this.startUserId = t[1];
      } else {
        this.bussinessWhere = bussinessWhere;
      }
    }
    if (session.getItem('month')) {
      this.startDate = JSON.parse(session.getItem('month')).startDate;
      this.endDate = JSON.parse(session.getItem('month')).endDate;
      this.bussinessWhere = "'" + this.startDate + "' <" + "create_date" + ' and create_date' + "<'" + this.endDate + "'"
    }
    if (this.bussinessWhere) {
      bussinessWhere = this.bussinessWhere
    }
    if (this.startUserId) {
      startUserId = this.startUserId
    }
    let pram = {
      nodeType,
      isEndProcess: true,
      userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
      pageRows: 10000,
      curPage: 1,
      bussinessWhere,
      startUserId
    }
    this.customeHttpService.bpmQueryTaskOnlyCountGroup(pram).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.pageData.result && data.pageData.result.length > 0) {
          this.list = data.pageData.result;
          this.approve(this.list)
        } else {
          this.nodata = true;
          $('#appR').height(700);
          this.maskLayer = false;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }
  approve(list) {
    let approvelist = []
    if (this.list.length > 0) {
      for (let i = 0; i < this.list.length; i++) {
        let element = this.list[i];
        let filterResult = approvelist.filter(x => x.bigClassNo == element['bigClassNo']);
        let exist = filterResult && filterResult.length;
        let temp = null;
        if (exist) {
          temp = {
            littleClassName: element['littleClassName'],
            littleClassNo: element['littleClassNo'],
            sysCount: element['sysCount'],
            timeOutCount: element['timeOutCount'],//
            urgentCount: element['urgentCount'],//
            systemCode: element['systemCode']
          }
          filterResult[0].childpage.push(temp);
          filterResult[0].bigsysCount = filterResult[0].bigsysCount + element['sysCount']
        } else {
          temp = {
            bigClassName: element['bigClassName'],
            bigClassNo: element['bigClassNo'],
            bigsysCount: element['sysCount'],
            bigtimeOutCount: element['timeOutCount'],//
            bigurgentCount: element['urgentCount'],//
            childpage: [
              {
                littleClassName: element['littleClassName'],
                littleClassNo: element['littleClassNo'],
                sysCount: element['sysCount'],
                timeOutCount: element['timeOutCount'],//
                urgentCount: element['urgentCount'],//
                systemCode: element['systemCode']
              }
            ]
          }
          approvelist.push(temp);
        }
      }
      this.ulData = approvelist;
      let session = window.sessionStorage;
      if (session.getItem('workliuCode')) {
        let f = session.getItem('workliuCode');
        let d = f.split('&');
        if (!approvelist[d[0]]) {
          d[0] = '0'; d[1] = '0';
        } else if (!approvelist[d[1]]) {
          d[1] = '0'
        }
        let a = d[0] + '&' + d[1];
        session.setItem('workliuCode', a);
        this.inNav = approvelist[d[0]].childpage[d[1]];
      } else {
        this.inNav = approvelist[0].childpage[0];
      }
      setTimeout(() => {
        this.start();
        this.maskLayer = false;
      }, 1500);
      this.disinNav = true;
    }
  }
  navclick(item) {
    if (this.commfunc.getSessionDataCH('workliuCode')) {
      this.commfunc.clearSession('workliuCode');
      this.commfunc.clearSession('workliuData');
    }
    for (let t = 0; t < this.ulData.length; t++) {
      for (let j = 0; j < this.ulData[t].childpage.length; j++) {
        if (this.ulData[t].childpage[j].littleClassNo == item.littleClassNo) {
          let session = window.sessionStorage;
          let a = t + '&' + j;
          let b = JSON.stringify(item);
          session.setItem('workliuCode', a);
          session.setItem('workliuData', b);
          break;
        }
      }
    }
    this.inNav = item
  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }
  start() {
    $(document).ready(function () {
      let session = window.sessionStorage;
      if (session.getItem('bussinessWhere')) {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      } else if (session.getItem('workliuCode')) {
        let f = session.getItem('workliuCode');
        let d = f.split('&');
        $('.menu_list ul li').eq(d[0]).children('.div1').show().children('.zcd').eq(d[1]).addClass('removes')
      } else {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      }
      //
      $(".menu_list ul li").click(function () {
        //
        if ($(this).children(".div1").is(":hidden")) {
          //
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(180deg)' });
            //
            $(this).children(".div1").animate({
              height: 'show'
            }, 200)
              //siblingsdiv1
              .end().siblings().find(".div1").hide(200);
            $(this).siblings().children(".xiala").css({ 'transform': 'rotate(360deg)' });
          }
        } else {
          //
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(360deg)' });
            $(this).children(".div1").animate({
              height: 'hide'
            }, 200)
              .end().siblings().find(".div1").hide(200);
          }
        }
      });

      //
      $('.div1').click(function (e) {
        e.stopPropagation();
      });

      //
      $(".menu_list ul li .div1 .zcd").click(function () {
        //
        $(this).addClass("removes").siblings().removeClass("removes");
        //
        $(".div1").each(function () {
          //
          if ($(this).is(":hidden")) {
            //
            $(this).children(".zcd").removeClass("removes");
          }
        });
      });
    });
  }
}
